.rodape-container {
  background-color: #212930;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.rodape-assinatura {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  margin-bottom: 24px;
  padding: 24px;
  color: #fff;
}


.rodape-assinatura-cabecalho {
  margin-bottom: 24px;
  font-size: 24px;
}

.rodape-assinatura-texto {
  margin-bottom: 24px;
  font-size: 20px;
}

.rodape-input {
  padding: 8px 20px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}

.rodape-links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.rodape-link-wrapper {
  display: flex;
}

.rodape-link-items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}

.rodape-link-items h2 {
  margin-bottom: 16px;
}

.rodape-link-items > h2 {
  color: #fff;
}

.rodape-link-items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.rodape-link-items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

.rodape-email-form h2 {
  margin-bottom: 2rem;
}

.rodape-input::-webkit-input-placeholder {
  color: #000;
}

.rodape-input:-ms-input-placeholder {
  color: #000;
}

.rodape-input::placeholder {
  color: #000;
}

